import { Component } from 'react';
import { connect } from 'dva';
import {
  Form,
  Input,
  Tooltip,
  Icon,
  Select,
  Button,
  message,
} from 'antd';
import { setFormVerticalLayout } from '@/utils/formLayout';

const { formItemLayout, tailFormItemLayout } = setFormVerticalLayout(6, 6, 6, 6, 6);
const { Option } = Select;

@connect(({ userManage, loading }) => ({
  userInfo: userManage.updateInfo,
  loading: loading.effects['register/userManage'],
}))
class UpdateForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      confirmDirty: false,
    };
  }

  cancelUpdate = () => {
      this.props.dispatch({
        type: 'userManage/hideUpdate',
      });
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (err) {
        message.error('');
        return;
      }
      const { _id } = this.props.userInfo;
      const data = {
        _id,
        name: values.name,
        email: values.email,
        password: values.password,
        phone: values.phone,
        prefix: values.prefix,
      };
      this.props.dispatch({
        type: 'userManage/showUpdateSubmit',
        payload: {
          data: { ...data },
        },
      });
    });
  };

  handleConfirmBlur = e => {
    const { value } = e.target;
    this.setState(prevState => ({ confirmDirty: prevState.confirmDirty || !!value }));
  };

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback('!');
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.props;
    const { name, email, password, phone, prefix } = this.props.userInfo;
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: `${prefix}`,
    })(
      <Select style={{ width: 70 }}>
        <Option value="86">+86</Option>
        <Option value="87">+87</Option>
      </Select>,
    );

    return (
      <Form {...formItemLayout} onSubmit={this.handleSubmit}>
        <Form.Item
          label={
            <span>
              &nbsp;
              <Tooltip title="">
                <Icon type="question-circle-o" />
              </Tooltip>
            </span>
          }
        >
          {getFieldDecorator('name', {
            initialValue: name,
            rules: [{ required: true, message: '', whitespace: true }],
          })(<Input allowClear />)}
        </Form.Item>
        <Form.Item label="">
          {getFieldDecorator('email', {
            initialValue: email,
            rules: [
              {
                type: 'email',
                message: '',
              },
              {
                required: true,
                message: '',
              },
            ],
          })(<Input allowClear />)}
        </Form.Item>
        <Form.Item label="" hasFeedback>
          {getFieldDecorator('password', {
            initialValue: password,
            rules: [
              {
                required: true,
                message: '',
              },
              {
                validator: this.validateToNextPassword,
              },
            ],
          })(<Input.Password allowClear />)}
        </Form.Item>
        <Form.Item label="" hasFeedback>
          {getFieldDecorator('confirm', {
            initialValue: password,
            rules: [
              {
                required: true,
                message: '',
              },
              {
                validator: this.compareToFirstPassword,
              },
            ],
          })(<Input.Password onBlur={this.handleConfirmBlur} allowClear />)}
        </Form.Item>
        <Form.Item label="">
          {getFieldDecorator('phone', {
            initialValue: phone,
            rules: [{ required: true, message: '' }],
          })(<Input addonBefore={prefixSelector} style={{ width: '100%' }} />)}
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" loading={loading} htmlType="submit">
            
          </Button>
          &nbsp;
          <Button onClick={this.cancelUpdate}>
            
          </Button>
        </Form.Item>
      </Form>
    );
  }
}
const UpdateContaienr = Form.create({ name: 'update' })(UpdateForm);

export default UpdateContaienr;
